$black: #0c080e;
$white: #ffffff;
$yellow: #ffca0d;
$green: #008281;
$grey: #dadada;
$red: #EB4A4B;

$font-thin: 'Roboto-Thin';
$font-light: 'Roboto-Light';
$font-regular: 'Roboto-Regular';
$font-semibold: 'Roboto-Semibold';
$font-bold: 'Roboto-Bold';
$font-extrabold: 'Roboto-Extrabold';
$default-font-color: $black;
$default-line-height: 1.5;

$font-small: 12px;
$font-medium: 16px;
$font-big: 20px;

$max-width: 1100px;
* {
  font-family: 'Roboto-Light';
	line-height: 1;
	color: black;
}

.heading {
	line-height: 1.5;
}

.heading--h1, h1 {
	font-size: 40px;
}

.heading--h2, h2 {
	font-size: 30px;
}

.heading--h3, h3 {
	font-size: 25px;
}

.heading--h4, h4 {
	font-size: 20px;
	font-family: 'Roboto-Bold';
}



* {
  margin: 0;
  box-sizing: border-box;
	appearance: none;
  font-family: 'Roboto-Thin';
  scrollbar-width: none;
  scroll-behavior: smooth;
}

*::-webkit-scrollbar {
  display: none;
}
%svg-common {
	background: url("sprite.svg") no-repeat;
}

.svg-general--arrow-link {
	@extend %svg-common;
	background-position: 88.23529411764706% 67.9245283018868%;
}

.svg-general--arrow-link-dims {
	width: 8px;
	height: 14px;
}

.svg-general--arrow-simple {
	@extend %svg-common;
	background-position: 100% 0;
}

.svg-general--arrow-simple-dims {
	width: 24px;
	height: 24px;
}

.svg-general--check {
	@extend %svg-common;
	background-position: 100% 25%;
}

.svg-general--check-dims {
	width: 24px;
	height: 24px;
}

.svg-general--close {
	@extend %svg-common;
	background-position: 100% 50%;
}

.svg-general--close-dims {
	width: 24px;
	height: 24px;
}

.svg-sprite--sprite {
	@extend %svg-common;
	background-position: 0 0;
}

.svg-sprite--sprite-dims {
	width: 120px;
	height: 120px;
}


.button {
	padding: 7.5px 20px;
	border-radius: 7.5px;
	border-width: 1px;
	border-style: solid;
	border-color: $green;
	color: $green;
	background-color: $white;
	display: inline-block;
	cursor: pointer;	
	text-decoration: none;

	&--green {
		background-color: $green;
		color: $white;
	}
	
	&--white {
		background-color: $white;
		color: $green;
	}
}

%svg-common {
	background-image: url("/media/icons/sprite/sprite.svg");
}

svg {
	fill: red;
}

.link {
	
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
	align-items: center;
	text-decoration:  none;
	
	&:hover {

		.link__arrow {
			margin-left: 6px;
			display: block;
		}
	
	}
	
	&__text {
		margin-right: 8px;
		font-family: 'Roboto-Regular';
	}

	&__arrow  {
		width: 16px;
		height: 16px;
		object-fit: contain;
		transition: all 0.1s ease;
	}

}